<template>
  <div>
    <x-header :left-options="{showBack: true}">
      <a slot></a>
    </x-header>
    <div class="main-body">
      <group>
        <cell title="" is-link link="/ctype"></cell>
        <switch title="" :value="is_start_record" @on-change="change"></switch>
        <!--<cell title="" is-link></cell>-->
        <divider></divider>
        <cell title="Q-Wilson" link="http://wpa.qq.com/msgrd?v=3&uin=527622467&site=qq&menu=yes">
          <!--<img slot="icon" src="../assets/qq.gif" alt="">-->
        </cell>
        <cell title="Q-" link="http://wpa.qq.com/msgrd?v=3&uin=1048247193&site=qq&menu=yes">
          <!--<img slot="icon" src="../assets/qq.gif" alt="">-->
        </cell>
      </group>
    </div>

  </div>

</template>
<script>
import { Cell, Group, XHeader, Divider, Switch} from 'vux/src/components'
  import getters from '_vuex/getters'
  import actions from '_vuex/actions'
export default {
  data() {
    return {

    }
  },
  components: {XHeader,Cell,Group,Divider,Switch},
  ready(){

  },
  vuex: {
    getters,actions
  },
  methods: {
    change: function(){
      this.user.is_start_record = !this.user.is_start_record
        this.$store.dispatch("USER",{user: this.user})
    }
  }
}


</script>
